@use "_reset";
@use "_variables";
@use "_utilities";

/* -------------------------------------------------------------------------- */
/*                                 Base Styles                                */
/* -------------------------------------------------------------------------- */

body {
	color: var(--foreground-color);
	background-color: var(--background-color);
}

.main,
.footer {
	background-color: var(--background-color);
}

.main {
	overflow-x: clip;
}

.section {
	padding: var(--unit-D) var(--layout-unit);
}

.section * {
	position: relative;
	z-index: calc(var(--base-zIndex) + 1);
}

.home--work__svg {
	max-width: 200px;
	border-radius: 999rem;
}

.svg-circle {
	fill: none;
	stroke-width: 2;
	stroke: var(--foreground-color);
}

.svg-tpath {
	dominant-baseline: central;
}

.svg-tspan {
	color: var(--foreground-color);
	font-size: 17px;
	text-transform: uppercase;
	letter-spacing: 0.049em;
}

/* -------------------------------------------------------------------------- */
/*                                   Header                                   */
/* -------------------------------------------------------------------------- */

.header {
	padding: var(--layout-unit);
	position: fixed;
	z-index: 100;
}

.header__content {
	pointer-events: none;
	position: fixed;
	inset-inline-start: 0;
	inset-block-end: 0rem;
	width: 100%;
	padding: 0 var(--unit) calc(var(--unit-B) + var(--unit-E));
}

.logotype {
	fill: var(--foreground-color);
	height: calc(0.75 * var(--unit-C));
	width: auto;
}

.toggle--menu::after {
	z-index: var(--nav-zIndex);
	content: "";
	display: block;
	position: fixed;
	inset-block-end: 0;
	inset-inline-start: 0;
	height: 100%;
	width: 100%;
	background: var(--bg-glass);
	opacity: 0;
	transition: opacity 300ms var(--ease-curve-B);
	pointer-events: none;
}

.toggle--menu:checked::after {
	opacity: 1;
	pointer-events: all;
}

.content__wrapper {
	width: 100%;
}

.content__wrapper,
.nav__wrapper {
	display: flex;
	flex-direction: column;
	gap: var(--unit-F);
}

.nav {
	z-index: calc(var(--nav-zIndex) + 1);
	order: 5;
	inset-inline-start: 0;
	inset-block-end: 0rem;
}

.theme__input {
	position: fixed;
	inset-block-start: var(--layout-unit);
	inset-inline-end: var(--layout-unit);
	display: flex;
}

.theme__input::before {
	content: "ðŸŒ™ ";
	margin-block-start: 0.15rem;
	margin-inline-end: 0.25rem;
}

.nav__item {
	pointer-events: all;
	color: var(--background-color);
	background: var(--fg-glass);
	border: 1px solid rgb(36, 36, 36);
	border-radius: var(--unit-F);
	backdrop-filter: blur(var(--blur-strength));
	opacity: 0;
	transition: all 750ms var(--ease-curve-A);
	transform: translateY(1em);
}

.nav__item:nth-child(3) {
	transition-delay: 50ms;
}

.nav__item:nth-child(2) {
	transition-delay: 100ms;
}

.nav__item:nth-child(1) {
	transition-delay: 150ms;
}

.nav__link {
	pointer-events: none;
	display: block;
	padding: 1rem var(--unit) 0.85rem var(--unit);
	letter-spacing: 0.05em;
	text-transform: uppercase;
	text-decoration: none;
	text-underline-offset: 0.5rem;
	color: var(--background-color);
}

/* -------------------------------------- */
/*         Navigation Menu Toggle         */

.toggle--menu {
	pointer-events: none;
	visibility: none;
	all: unset;
	overflow: hidden;
}

.toggle--menu--label {
	pointer-events: all;
	z-index: calc(var(--nav-zIndex) + 1);
	position: fixed;
	inset-block-end: -4.5rem;
	inset-inline-end: 50%;
	transform: translateX(50%);
	cursor: pointer;
	display: block;
	background: var(--bg-glass);
	width: var(--unit-A);
	height: var(--unit-A);
	border-radius: 50%;
	border: 1px solid lightgray;
	backdrop-filter: blur(var(--blur-strength));
	filter: drop-shadow(0px 4px 10px rgba(121, 121, 121, 0.35));
	transition: all 750ms var(--ease-curve-B);
}

.toggle--menu--label::before,
.toggle--menu--label::after {
	position: absolute;
	text-transform: uppercase;
	display: block;
	text-align: center;
	width: 100%;
	padding-top: calc(var(--unit-F) * 1.7);
}

.toggle--menu--label::before {
	content: "Menu";
	color: var(--foreground-color);
}

.toggle--menu--label::after {
	content: "âœ–";
	font-size: 1.4em;
	color: var(--background-color);
	opacity: 0;
	padding-top: calc(var(--unit-F) * 1.125);
}

.toggle--menu:checked ~ .content__wrapper ~ .toggle--menu--label {
	border-color: rgb(39, 39, 39);
	background-color: var(--fg-glass);
	inset-block-end: var(--unit);
	width: var(--unit-C);
	height: var(--unit-C);
}

.toggle--menu:checked ~ .content__wrapper ~ .toggle--menu--label::before {
	opacity: 0;
}

.toggle--menu:checked ~ .content__wrapper ~ .toggle--menu--label::after {
	opacity: 1;
}

.toggle--menu ~ .content__wrapper .nav__item {
	pointer-events: none;
}

.toggle--menu:checked ~ .content__wrapper .nav__link {
	pointer-events: all;
}

.toggle--menu:checked ~ .content__wrapper .nav__item {
	transform: translateY(0em);
	opacity: 1;
	pointer-events: unset;
}

/* -------------------------------------- */
/*              Theme Toggle              */

.toggle--theme {
	display: none;
}

.toggle--theme--label {
	pointer-events: all;
	cursor: pointer;
	display: flex;
	align-items: center;
	margin-inline-start: auto;
	padding: 3px 4px;
	height: 1.75rem;
	width: 3rem;
	background-color: var(--background-color);
	border: 2px solid var(--foreground-color);
	border-radius: 2rem;
}

.toggle--theme--label::after {
	content: "";
	display: block;
	aspect-ratio: 1;
	border-radius: 2rem;
	background-color: var(--foreground-color);
	height: 100%;
	width: 50%;
	aspect-ratio: 1;
	transition: all 300ms var(--ease-curve-B);
}

.toggle--theme[type="checkbox"]:checked + .toggle--theme--label::after {
	transform: translateX(100%);
}

/* -------------------------------------------------------------------------- */
/*                                 Main - Hero                                */
/* -------------------------------------------------------------------------- */

.main__hero-text {
	display: flex;
	align-items: center;
	padding-block: var(--unit-A) var(--unit-B);
	min-height: 55vh;
	font-family: var(--ff-A);
	font-size: var(--fs-A);
	line-height: 1.2;
	letter-spacing: -0.025em;
	max-width: 23ch;
}

.home--projects {
	padding-top: 0;
	position: relative;
}

/* -------------------------------------------------------------------------- */
/*                               Main - Projects                              */
/* -------------------------------------------------------------------------- */

@keyframes simpleSpin {
	0% {
		transform: translate(40%, -50%) rotate(0deg);
	}
	100% {
		transform: translate(40%, -50%) rotate(360deg);
	}
}

.home--work__svg {
	z-index: 10;
	position: absolute;
	inset-inline-end: 0;
	inset-block-start: 0;
	transform: translate(40%, -50%);
	animation: simpleSpin 30s linear 0s infinite;
}

.project-card {
	display: flex;
	flex-direction: column;
}

.project-card__text {
	order: 2;
}

/* -------------------------------------------------------------------------- */
/*                                   Footer                                   */
/* -------------------------------------------------------------------------- */

.footer {
	position: relative;
	// display: grid;
	display: flex;
	flex-direction: column;
	// grid-template-columns: var(--base-columns);
	gap: calc(2 * var(--layout-unit));
	// padding: var(--unit-C) var(--layout-unit);
	padding: var(--unit-C) var(--layout-unit) calc(1 * var(--layout-unit));
	border-radius: 0 0 var(--unit-E) var(--unit-E);
}

.footer::before {
	content: "";
	position: absolute;
	top: 0;
	display: block;
	height: 0.25rem;
	// width: 100%;
	inset-inline: var(--layout-unit);
	// max-width: calc(100% - var(--layout-unit) * 2);
	border-top: 1px solid var(--foreground-color);
	padding-bottom: var(--layout-unit);
}

.icon {
	border: 2px solid var(--foreground-color);
	border-radius: 99vw;
	height: var(--unit-D);
	width: var(--unit-D);
}

.icon-fblock,
.link-fblock,
.extlink-fblock {
	grid-column-end: span 2;
}

.attr-fblock {
	grid-column-start: -1;
}

.ftr-header {
	text-transform: uppercase;
	font-weight: bold;
	margin-bottom: 1em;
}

.list-item {
	margin-block: 0.5em;
}

/* -------------------------------------------------------------------------- */
/*                                 FOOTER BAR                                 */

@keyframes marquee-anim {
	0% {
		transform: translateX(0%);
	}

	100% {
		transform: translateX(-100%);
	}
}

.footer__bar {
	background-color: var(--foreground-color);
	color: var(--background-color);
	overflow-x: hidden;
	white-space: nowrap;
	//
	padding-block: var(--unit-B) var(--unit-B);
	position: sticky;
	bottom: 0;
	margin-top: calc(-1 * var(--unit-D));
	z-index: -1;
}

.footer__bar:hover > div {
	animation-play-state: paused;
}

.marquee {
	display: inline-block;
	animation: marquee-anim 70s linear infinite;
}

.marquee__txt::after {
	content: "\2014";
	display: inline-block;
	padding-inline: 2rem;
}

.footer__bar::after {
	content: "";
	display: block;
	width: 100%;
	background-color: (--foreground-color);
}

